.App {
  text-align: center;
}

path {
  fill: none;
  stroke: #000;
}

.content-container {
  display: table;
  width: 100%;
}

.leaderboard {
  display: table-cell;
  vertical-align: top;
  padding-left: 10px;
  padding-right: 20px;
  padding-top: 15px;
}

.leader-content p {
  text-align: left;
  margin-bottom: 10px;
}

.leader-content {
  margin: 0 auto;
  display: inline-block;
  float: none;
}

.leader-content-container {
  width: 100%;
  text-align: center;
}

.podium-container {
  width: 100%;
  text-align: center;
}

.podium {
  margin: 0 auto;
  display: table;
  float: none;
}

.podium p {
  text-align: left;
  margin-bottom: 10px;
}

h3 {
  text-align: center;
}

svg {
  display: table-cell;
}

p {
  margin: 0 auto;
  text-align: center;
}

body {
  background-color: #f6f7f9;
}

.btn {
  border-radius: 10px;
  color: #ffffff;
  font-size: 20px;
  background: #2790d6;
  padding: 10px 20px 10px 20px;
  border-style: none;
  display: block;
  margin: 0 auto;
  margin-top: 20px;
}

#intro {
  text-align: center;
  width: 80%;
  margin: 0 auto;
}

.btn:focus{
  outline: none;
  border: 1px solid black;
  padding: 9px 19px 9px 19px;
}

.btn-active {
  cursor: pointer;
}

.btn-active:hover {
  background: #3cb0fd;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3cb0fd), to(#3498db));
  background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
}



